import React, { useState } from 'react';

const MatrixCalculator = () => {
  const [arrays, setArrays] = useState([[]]);
  const [input, setInput] = useState('');
  const [calculationMode, setCalculationMode] = useState(false);
  
  const maxLength = arrays[0]?.length || 0;

  const handleNumberClick = (num) => {
    if (!calculationMode) {
      setArrays((prev) => {
        const newArrays = [...prev];
        const currentRow = newArrays[newArrays.length - 1];
        if (currentRow.length < maxLength || newArrays.length === 1) {
          currentRow.push(num);
        }
        return newArrays;
      });
    } else {
      setInput((prev) => prev + num);
    }
  };

  const handleOperatorClick = (operator) => {
    setInput((prev) => prev + operator);
  };

  const handleAddRow = () => {
    if (arrays[arrays.length - 1].length === maxLength || arrays.length === 1) {
      setArrays((prev) => [...prev, []]);
    }
  };

  const handleProceedToCalculation = () => {
    setCalculationMode(true);
  };

  const handleEqualClick = () => {
    // Perform the calculation based on the input string.
    // This is a basic example, you can extend it to support matrix-specific calculations.
    try {
      const result = eval(input);  // Use eval carefully, sanitize inputs in production
      setArrays([...arrays, result]);
    } catch (error) {
      console.error("Invalid calculation", error);
    }
  };

  const handleClear = () => {
    setInput('');
  };

  const handleNewCalculation = () => {
    window.location.reload();  // refreshes the page
  };

  return (
    <div>
      {arrays.map((arr, idx) => (
        <div key={idx}>
          {arr.map((num, i) => <span key={i}>{num}</span>)}
        </div>
      ))}
      
      {calculationMode ? (
        <div>
          <div>{input}</div>
          <div style={{ display: 'flex', overflowX: arrays.length > 4 ? 'scroll' : 'hidden' }}>
            {arrays.length >= 4
              ? arrays.map((arr, idx) => (
                <button key={idx} disabled>{`Array ${idx + 1}`}</button>
              ))
              : <button disabled>Add more arrays</button>}
          </div>
          
          <div>
            {[...Array(10).keys()].map((num) => (
              <button key={num} onClick={() => handleNumberClick(num)}>{num}</button>
            ))}
          </div>

          <div>
            <button onClick={handleClear}>Clear</button>
            <button onClick={handleEqualClick}>=</button>
            <button onClick={() => handleOperatorClick('+')}>+</button>
            <button onClick={() => handleOperatorClick('-')}>-</button>
            <button onClick={() => handleOperatorClick('*')}>*</button>
            <button onClick={() => handleOperatorClick('/')}>/</button>
          </div>
          <button onClick={handleNewCalculation}>New Calculation</button>
        </div>
      ) : (
        <div>
          <div>
            {[...Array(10).keys()].map((num) => (
              <button key={num} onClick={() => handleNumberClick(num)}>{num}</button>
            ))}
          </div>
          <button onClick={handleAddRow}>Add New Array</button>
          <button onClick={handleProceedToCalculation} disabled={arrays.length < 4}>Proceed to Calculation</button>
        </div>
      )}
    </div>
  );
};

export default MatrixCalculator;
